import React from 'react';
import { Row, Col, Card, Table, Modal, Button } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import { getVendors, deleteVendors } from '../../api/inventory/vendorApi';
import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Loader from '../../App/components/Reusables/Loader';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Swal from 'sweetalert2';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { getPurchaseOrders, deletePurchaseOrder } from '../../api/inventory/purchaseOrder';
import Num from '../../App/components/Reusables/Num';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import PurchaseOrdersFilter from '../../App/components/SearchFilters/PurchaseOrdersFilter';
import { getPurchaseOrdersSearchFilterObject } from '../../helpers/getSearchFilterObject';
import { getStartEnd } from '../../helpers/getStartEnd';

const PurchaseOrders = (props) => {

    const [loading, setLoading] = React.useState(true)
    const [purchaseOrdersArr, setPurchaseOrdersArr] = React.useState([]);
    const [deleteData, setDeleteData] = React.useState({ showModal: false, purchaseOrderId: null })
    const [instanceCount, setInstanceCount] = React.useState(0)
    // const match = React.useMatch()

    React.useEffect(() => {
        const controller = new AbortController();
        const requestJson = {
            "action": "get",
            "filter": "none",
            ...getPurchaseOrdersSearchFilterObject(props),
            ...getStartEnd(props)
        };
        console.log(JSON.stringify(requestJson))
        getPurchaseOrders(requestJson, controller.signal)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    setPurchaseOrdersArr(res.json.purchase_orders);
                    setInstanceCount(res.json.count);
                    setLoading(false);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch((err) => {
                console.log(err)
            });
        return () => controller.abort();
    }, []);



    const _triggerDeleteModal = (id) => {
        setDeleteData({ ...deleteData, showModal: true, purchaseOrderId: id })
    }

    const _delete = (idToBeDeleted) => {
        console.log(idToBeDeleted);
        deletePurchaseOrder({ action: 'delete', purchase_orders_id: [idToBeDeleted] })
            .then(async (res) => {
                if (res.json.status) {
                    const deletedOrder = purchaseOrdersArr.find(order => order.id === idToBeDeleted);
                    let tempArr = purchaseOrdersArr;
                    tempArr.splice(tempArr.indexOf(deletedOrder), 1);
                    await setPurchaseOrdersArr([...tempArr])
                    await setDeleteData({ ...setDeleteData, showModal: false, purchaseOrderId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Purchase order deleted successfully!', background: '#dffff0' })
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
    }


    const PurchaseOrdersList = purchaseOrdersArr.map((order, index) => {
        return (
            <tr key={index}

                style={{ cursor: 'pointer' }}
                onClick={(e) => {
                    props.history.push('/purchase-order/' + order.id)
                }}
            >
                <th scope="row">{order.id}</th>

                <td
                >{order.vendor_name}</td>
                {/* <td style={{ maxWidth: '200px', overflow: 'hidden', paddingRight: '50px' }}>{order.items}</td> */}
                <td>{(new Date(order.invoiced_on)).toDateString()}</td>
                <td><Num>{order.total_cost}</Num></td>
                <td>{order.status_name}</td>

                <td>
                    <i
                        className="feather icon-edit text-c-green f-19 m-r-10"
                        style={{ cursor: 'pointer' }}
                        onClick={(e) => {
                            e.stopPropagation();
                            props.history.push('/purchase-order/update/' + order.id)
                        }
                        }
                    />
                    <i
                        className="feather icon-trash-2 text-c-red f-19 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={(e) => {
                            e.stopPropagation();
                            _triggerDeleteModal(order.id)
                        }}
                    />
                </td>
            </tr>
        )
    })

    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Purchase Orders</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`purchase-order/create`)}
                    > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                </div>
            </Row>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <PurchaseOrdersFilter />
                            <Table hover responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Vendor Name</th>
                                        {/* <th>Items</th> */}
                                        <th>Invoiced On</th>
                                        <th>Total Cost</th>
                                        <th>Status</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {PurchaseOrdersList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(purchaseOrdersArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />
                        </Card.Body>

                    </Card >


                    <DeleteModal
                        show={deleteData.showModal}
                        onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                        onDelete={() => _delete(deleteData.purchaseOrderId)}
                    />


                </Col>
            </Row>
        </Aux>
    );
}

export default PurchaseOrders;